import ReactDOM from "react-dom"
import React, { Suspense, useEffect, useRef, useMemo, useState } from "react"
import { Canvas, Dom, useLoader, useFrame } from "react-three-fiber"
import { TextureLoader, LinearFilter } from "three"
import lerp from "lerp"
import ModalVideo from 'react-modal-video'
// import { Text, MultilineText } from "./components/Text"
import { Text } from "./components/Text"
// import Diamonds from "./diamonds/Diamonds"
import Plane from "./components/Plane"
import { Block, useBlock } from "./blocks"
import state from "./store"
import '../node_modules/react-modal-video/css/modal-video.min.css'
import "./styles.css"
 
function Startup() {
  const ref = useRef()
  useFrame(() => (ref.current.material.opacity = lerp(ref.current.material.opacity, 0, 0.025)))
  return <Plane ref={ref} color="#0e0e0f" position={[0, 0, 200]} scale={[100, 100, 1]} />
}

function Paragraph({ image, index, offset, factor, header, aspect, url, text }) {
  const { contentMaxWidth: w, canvasWidth, margin, mobile } = useBlock()
  const size = aspect < 1 && !mobile ? 0.65 : 1
  const alignRight = (canvasWidth - w * size - margin) / 2
  const pixelWidth = w * state.zoom * size
  const left = !(index % 2)
  const color = index % 2 ? "#D40749" : "#2FE8C3"
  return (
    <Block factor={factor} offset={offset * 0.625 + 0.0125}>
      <group position={[left ? -alignRight : alignRight, 0, 0]}>
        <Plane map={image} args={[1, 1, 32, 32]} shift={75} size={size} aspect={aspect} scale={[w * size, (w * size) / aspect, 1]} frustumCulled={false}
        />
        <Dom
          style={{ width: pixelWidth / (mobile ? 1 : 2), textAlign: left ? "left" : "right" }}
          position={[left || mobile ? (-w * size) / 2 : 0, (-w * size) / 2 / aspect - 0.4, 1]}>
          <div tabIndex={index}>
            {/* {text} */}
            {url ? <a href={url} className="modal-trigger" target="_blank" data-url={url} >Clique aqui para assistir.</a>: ""}
          </div>
        </Dom>
        <Text left={left} right={!left} size={w * 0.04} color={color} top position={[((left ? -w : w) * size) / 2, (w * size) / aspect / 2 + 0.5, -1]}>
          {header}
        </Text>
        <Block factor={0.2}>
          <Text opacity={0.5} size={w * 0.15} color="#1A1E2A" position={[((left ? w : -w) / 2) * size, (w * size) / aspect / 1.75 + 0.15, -10]}>
            {String(index + 1).padStart(2,'0')}
          </Text>
        </Block>
      </group>
    </Block>
  )
}

function Content() {
  const images = useLoader(
    TextureLoader,
    state.paragraphs.map(({ image }) => window.location.pathname + image)
  )
  useMemo(() => images.forEach(texture => (texture.minFilter = LinearFilter)), [images])
  const { contentMaxWidth: w, canvasWidth, canvasHeight, mobile } = useBlock()
  return (
    <>
      <Block factor={1} offset={0}>
        <Block factor={1.4}>
          <Text left size={w * 0.25} position={[-w / 3.2, 1, -1]} color="#2FE8C3">
            50
          </Text>
        </Block>
        <Block factor={1.2}>
          <Text left size={w * 0.105} position={[-w / 3.2, -1, -1]} color="#d40749">
            Curtas
          </Text>
        </Block>
        <Block factor={1.0}>
          <Dom className="subtitle" position={[-w / 3.2, -w * 0.08 + 0.25 -1.15, -1]}>Um catálogo da história do cinema de animação{mobile ? <br /> : " "}no formato de código de barras.</Dom>
        </Block>
      </Block>
      {state.paragraphs.map((props, index) => (
        <Paragraph key={index} index={index} {...props} image={images[index]} />
      ))}
      {state.stripes.map(({ offset, color, height }, index) => (
        <Block key={index} factor={-1.25} offset={offset * 0.625}>
          <Plane args={[50, height, 32, 32]} shift={-4} color={color} rotation={[0, 0, Math.PI / 8]} position={[0, 0, -10]} />
        </Block>
      ))}
      {/* <Block factor={1.625} offset={50}>
        <Dom className="bottom-left" position={[-canvasWidth / 2, -canvasHeight / 2, 0]}>
          
        </Dom>
      </Block> */}
    </>
  )
}

function App() {
  // const scrollArea = useRef()
  // const onScroll = e => (state.top.current = e.target.scrollTop)
  // useEffect(() => void onScroll({ target: scrollArea.current }), [])

  const [modalUrl, setModalUrl] = useState("");

  useEffect(() => {
    const onScroll = e => (state.top.current = window.scrollY);
    window.addEventListener("scroll", onScroll);

    const onModalClick = (e) => {
      if (e.target.classList.contains("modal-trigger")) {
        e.preventDefault();
        const url = new URL(e.target.dataset.url);
        setModalUrl(url.searchParams.get('v'));
      }
    };
    document.getElementById("root").addEventListener("click", onModalClick);
   return () => {
    window.removeEventListener("scroll", onScroll);
    document.getElementById("root").removeEventListener("click", onModalClick)
   } 
  }, [])
  return (
    <>
      {/* <div className="scrollArea" ref={scrollArea} onScroll={onScroll}> */}
      <div className="scrollArea">
        {new Array(state.sections).fill().map((_, index) => (
          <div key={index} id={String(index).padStart(2,'0')} style={{ height: `${(state.pages / state.sections) * 62.5}vh` }} />
        ))}
        <div style={{ height: `${((state.pages / state.sections) * 62.5) * 0.625}vh` }} />
      </div>
      <Canvas className="canvas" style={{ position: "fixed" }} concurrent pixelRatio={1} orthographic camera={{ zoom: state.zoom, position: [0, 0, 500] }}>
        <Suspense fallback={<Dom center className="loading" children="Carregando..." />}>
          <Content />
          {/* <Diamonds /> */}
          <Startup />
        </Suspense>
      </Canvas>
      <div className="frame">
        <h1 className="frame__title">50 Curtas</h1>
        <div className="frame__links">
          <a className="frame__link" href="https://acemir.me">
            Acemir Sousa Mendes
          </a>
          {/* <a className="frame__link" href="http://tympanus.net/Tutorials/PhysicsMenu/">
            Previous demo
          </a>
          <a className="frame__link" href="https://tympanus.net/codrops/?p=45441">
            Article
          </a>
          <a className="frame__link" href="https://github.com/drcmda/the-substance">
            GitHub
          </a> */}
        </div>
        <div className="frame__copy">
          Copyleft <span className="copyleft">(<svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" viewBox="0 0 11 15.1"><path d="M5.2,7.1c1.2,0,2.1,0.4,2.7,1.1c0.7,0.7,1,1.7,1,2.9c0,1.3-0.3,2.2-1,2.9c-0.7,0.7-1.6,1.1-2.7,1.1c-0.8,0-1.5-0.2-2-0.6c-0.6-0.4-1-1-1.2-1.7L3,12.4c0.1,0.5,0.4,0.9,0.7,1.2C4.1,13.9,4.6,14,5.2,14C6,14,6.6,13.8,7,13.2c0.4-0.5,0.7-1.2,0.7-2.2c0-0.9-0.2-1.6-0.7-2.2C6.6,8.4,6,8.2,5.2,8.2c-1,0-1.7,0.5-2.1,1.5L2,9.1C2.3,8.5,2.7,8,3.2,7.6C3.7,7.2,4.4,7.1,5.2,7.1z"/></svg>)</span> 2022
        </div>
        <div className="frame__nav">
          <a className="frame__link" href="#00" children="Início" />
          {new Array(state.pages).fill().map((_, index) => {
            const item = String(index + 1).padStart(2,'0');
            return (
              <a key={index} className="frame__link"  href={`#${item}`} children={item} />
            );
          })}
        </div>
      </div>
      <ModalVideo channel='youtube' autoplay isOpen={!!modalUrl} videoId={modalUrl} youtube={{autoplay:1}} onClose={() => {
        setModalUrl("");
      }} />
    </>
  )
}

ReactDOM.render(<App />, document.getElementById("root"))
